$(".clients-data .th__check input").change(function () {
    let isChecked = $(this).prop("checked")

    $(".clients-data .td__check input").each(function (i,el) {
        $(el).prop("checked",isChecked)
    })

    actionPanelShow()
})

$(".clients-data .td__check input").change(function () {
    let clientsCount = $(".clients-data .td__check input").length
    let isCheckedElements = []

    $(".clients-data .td__check input").each(function (i,el) {
        if($(el).prop("checked")){
            isCheckedElements.push($(el).prop("checked"))
        }
    })

    if(isCheckedElements.length !== clientsCount){
        $(".clients-data .th__check input").prop("checked",false)
    } else{
        $(".clients-data .th__check input").prop("checked",true)
    }

    actionPanelShow()
})


function actionPanelShow() {
    let selectedClients = $(".clients-data .td__check input:checked").length
    console.log(selectedClients)
    $("._selected-clients-count").html(selectedClients)
    if(selectedClients>0){
        $(".action").addClass("_active")
    } else{
        $(".action").removeClass("_active")
    }
}
