$(".burger").click(function () {
    $(".sidebar").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})

$(".nav__show").click(function () {
    $(this).toggleClass("_active")
    $(this).siblings(".nav__hidden").slideToggle()
})

$("._open-notifications").click(function () {
    $(".notifications").addClass("_open")
})

$(".notifications__bg").click(function () {
    $(".notifications").removeClass("_open")
})
