$(document).ready(function() {
    fix100vh();


    $(".tag-select").select2({
        tags: true,
        tokenSeparators: [',', ' ']
    });

});


fix100vh();
function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

let datepickerLocal = {
    "format": "DD/MM/YYYY",
    "separator": " - ",
    "applyLabel": "Apply",
    "cancelLabel": "Cancel",
    "fromLabel": "C",
    "toLabel": "Lj",
    "customRangeLabel": "Custom",
    "daysOfWeek": [
        "Вс",
        "Пн",
        "Вт",
        "Ср",
        "Чт",
        "Пт",
        "Сб"
    ],
    "monthNames": [
        "Январь",
        "Февраль",
        "Март",
        "Апрель",
        "Май",
        "Июнь",
        "Июль",
        "Август",
        "Сентябрь",
        "Октябрь",
        "Ноябрь",
        "Декабрь"
    ],
    "firstDay": 1
}

$('.date-input').daterangepicker({
    "showDropdowns": true,
    "alwaysShowCalendars": true,
    "autoApply": true,
    "locale": datepickerLocal,
}, function(start, end, label) {

});

$('.date-input-single').daterangepicker({
    "singleDatePicker": true,
    "showDropdowns": true,
    "alwaysShowCalendars": true,
    "autoApply": true,
    "locale": datepickerLocal,
}, function(start, end, label) {

});
